body{
    margin: 0;
    padding: 0;
}

.container{
    background-color: bLACK;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    justify-items: center;
    height: 75px;

}

.container .logo{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: none;
    display: flex;
    margin-top: 4px;
    margin-left: 10px;
    cursor: pointer;

}
.container .Navbar ul{
    display: flex;
    flex-direction: row;
}

.container .Navbar ul li{
    list-style: none;
    margin-left: 20px;
    
}

.container .Navbar ul li a{
    text-decoration: none;
    color: white;
    font-size: 25px;
    margin-bottom: 20px;
    margin-right: 50px;
    margin-left: 200%;
    
    /* position: absolute; */
}

.container .Navbar ul li a:hover{
    color: blue;
    transition: ease-in-out 1s;
}

.container .Navbar{
    margin-right: 10px;
    /* position: relative; */
}

.container .Nama ul li a{
    margin-right: 100px;
}
.Navbar h1{
    color: white;
    position: absolute;
    left: 0px;
    margin-left: 20px;
}

