body{
    margin: 0;
    padding: 0;
}

.main h1{
    color: white;
    font-size: 40px;
    text-align: left;
    /* background-color: red; */
}

.containerMain  .main{
    background-color: #333;
    /* padding: 50px 100px; */
    height: 500px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.containerMain  .main div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: red; */
    width: 600px;
    height: 600px;
}
.containerMain  .main div .img{
    width: 200px;
    height: 200px;
    background-color: blue;
    border-radius: 50%;
    background-image: url(./rafi.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

/* .containerMain .main img{
    width: 300px;
    margin: 30px;
} */
.containerMain .main .img h3{
    font-weight: 700;
}
.containerMain .main a{
    position: relative;
    color: white;
    bottom: 30px;
}

.rasyajelek{
    position: relative;
    bottom: 35px;
}

.containerMain .main button{
    background-color: black;
    color: white;
    border: none;
    padding: 10px 10px;
    border-radius: 5px;
}
.containerMain .main button:hover{
    background-color: white;
    color: black;
    transition: 0.5s;
}
.containerMain .main div .icon{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* align-items: space-beetw; */
    width: 200px;
    height: 30px;
    /* background-color: red; */
}
.containerMain .main .danis{

    display: inline;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* background-color: black; */

}
.containerMain .main .danis:hover{
    color: black;
    transition: 0.5s;
    cursor: pointer;
}
.main p, h3{
    color: white;
}
